import { IDataConvert } from "../idataconvert";
import { isNullOrUndefined } from "util";
import { ParamMapping } from "./parammapping";

export class DMExtensionInfo implements IDataConvert {
    
    ModelId: string;
    ModelCode: string;
    ModelName: string;
    ModelPath: string;
    Mappings: Array<ParamMapping>;
    
    init(obj: object) {
        if (!isNullOrUndefined(obj)) {
            this.ModelId = obj["ModelId"] == null ? null : obj["ModelId"];
            this.ModelCode = obj["ModelCode"] == null ? null : obj["ModelCode"];
            this.ModelName = obj["ModelName"] == null ? null : obj["ModelName"];
            this.ModelPath = obj["ModelPath"] == null ? null : obj["ModelPath"];
            let mappingArray = new Array<ParamMapping>();
            if (!isNullOrUndefined(obj["Mappings"])) {
                obj["Mappings"].forEach(mappingObj => {
                    let mapping = new ParamMapping();
                    mapping.init(mappingObj);
                    mappingArray.push(mapping);
                });
            }
            this.Mappings=mappingArray;
        }
    }

    convertToJObject(): object {
        let obj=new Object();
        obj["ModelId"]=this.ModelId;
        obj["ModelCode"]=this.ModelCode;
        obj["ModelName"]=this.ModelName;
        obj["ModelPath"]=this.ModelPath;
        let mappingObjArray=new Array<Object>();
        if(this.Mappings!=null&&this.Mappings.length>0){
            this.Mappings.forEach(mapping => {
                mappingObjArray.push(mapping.convertToJObject());
            });
        }
        obj["Mappings"]=mappingObjArray;
        return obj;
    }
}


