import { BaseCodeRule } from "../base/basecoderule";
import { isNullOrUndefined } from "util";
import { DMExtensionInfo } from "./dmextensioninfo";

export class DMBaseCodeRule extends BaseCodeRule{
    ExtensionInfo:DMExtensionInfo;
    init(obj:Object){
        if(!isNullOrUndefined(obj)){
            super.init(obj);
            let extensionObj= obj["ExtensionInfo"];
            if(!isNullOrUndefined(extensionObj)){
                this.ExtensionInfo = new DMExtensionInfo();
                this.ExtensionInfo.init(extensionObj);
            }
        }
    }

    convertToJObject():Object{
        let obj=super.convertToJObject();
        if(this.ExtensionInfo!=null){
            obj["ExtensionInfo"]=this.ExtensionInfo.convertToJObject();
        }
        return obj;
    }
}


