import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';
import { OutParamSegment } from '../../../coderulemetadata/base/segment/outparamsegment';
import { CodeRuleService } from '../../../coderuleservice/coderuleservice';
import { EventBusService } from '../../../coderuleservice/eventBusService';
import { ExpressionService } from '@gsp-svc/expression/editor/expressionservice';
import { expression_entity } from '../expression/mockexpression';

@Component({
    selector: 'app-outparam',
    templateUrl: './outparam.component.html',
    providers: [ExpressionService]
})

export class OutParamSegmentComponent implements OnInit {
    /**
     * 编号规则元数据
    */
    @Input() segment: OutParamSegment;
    @Input() outParamExpress:string;

    segmentType:string;
    expressionEntity = expression_entity
    status: Array<boolean> = [true, false];

    constructor(private codeRuleService:CodeRuleService,private codeRuleEventBus:EventBusService,private expr: ExpressionService) { }

    ngOnInit(): void {
        let segmentTypeString =this.codeRuleService.getSegmentTypeToString(this.segment.getSegmentType());
        this.segmentType=this.codeRuleService.getSegmentTypeDesc(segmentTypeString);
    }

    outParamExpressChange(){
        this.codeRuleEventBus.eventbus.emit({
            outParamExpress:this.outParamExpress,
            eventName:"dmOutParamExpressEmit"
        })
    }

    openExpr(){
        //debugger;
        let exprText:string=this.outParamExpress?JSON.parse(this.outParamExpress ).Expr:"默认文本";
        this.expr.addExpressionText(exprText);
        this.expressionEntity.sessionEntities.forEach(
            item => this.expr.addSession(item));
        this.expressionEntity.contextEntities.forEach(
            item => this.expr.addContext(item)
        )
        this.expressionEntity.funcEntities.forEach(
            item => this.expr.addFunc(item)
        )
        this.expr.buildExpression().then(expressioninfo => {
        //debugger;
            this.outParamExpress =expressioninfo as string;
            this.codeRuleEventBus.eventbus.emit({
                outParamExpress:this.outParamExpress,
                eventName:"dmOutParamExpressEmit"
            })
        }
        );

    }


}


