import { ExpressionContextEntity } from "@gsp-svc/expression/editor/expressioncontextentity";
import { FuncType } from "@gsp-svc/expression/editor/funcentity";

export const expression_entity: ExpressionContextEntity = {
    contextEntities: [
      {
        key: "UserId",
        name: "用户Id",
        description: "当前用户的Id"
      },
      {
        key: "TenantId",
        name: "租户Id",
        description: "当前租户的名称"
      },
      {
        key: "CurrentSU",
        name: "服务单元",
        description: "当前所处的服务单元"
      },
      {
        key: "Language",
        name: "语言",
        description: "当前语言"
      },
      {
        key: "AppCode",
        name: "实例编号",
        description: "当前实例编号"
      }

    ],
    sessionEntities: [
      {
        key: "SessionId",
        name: "SessionId",
        description: "当前的SessionId"
      }
    ],
    funcEntities: [
      {
        key: "DefaultFunction.GetContextParameter()",
        params: "contextParam",
        name: "contextParam",
        description: "获取上下文参数",
        type: FuncType.Extend
      },
      {
        key: "DefaultFunction.GetSessionValue()",
        params: "sessionValue",
        name: "sessionValue",
        description: "获取Session信息",
        type: FuncType.Extend
      }
    ],
    expr: ""
  };


