import { IDataHandler } from "./idatahandler";
import { HandlerType } from "./handlertype";
import { isNullOrUndefined } from "util";

export class ExpressHandler implements IDataHandler{
   
    Express:string;

    getHandlerType(): HandlerType {
        //debugger;
        return HandlerType.Express;
    }
    
    init(obj: object) {
        if(!isNullOrUndefined(obj)){
            this.Express=obj["Express"]==null?null:obj["Express"];
        }
    }

    convertToJObject(): object {
        let obj=new Object();
        obj["Express"]=this.Express;
        return obj;
    }
}


