import { IDataConvert } from "./idataconvert";
import { CodeRuleType } from "./coderuletype";
import { AbstractionCodeRule } from "./base/abstractioncoderule";
import { isNullOrUndefined } from "util";
import { CodeRuleFactory } from "./coderulefactory";

/**
 * 编号规则元数据抽象类
 */
export  class CodeRuleMetadata implements IDataConvert {

    Id: string;
    Code: string;
    Name: string;
    Description: string;
    RuleType: CodeRuleType;
    CodeRule: AbstractionCodeRule;

    /**
     * 初始化
     * @param obj 编号规则元数据对象
     */
    init(obj: Object) {
        if (!isNullOrUndefined(obj)) {
            this.Id = obj["Id"] == null ? null : obj["Id"];
            this.Code = obj["Code"] == null ? null : obj["Code"];
            this.Name = obj["Name"] == null ? null : obj["Name"];
            this.Description = obj["Description"] == null ? null : obj["Description"];
            this.RuleType = obj["RuleType"] == null ? null : obj["RuleType"] as number;
            this.CodeRule=CodeRuleFactory.getEmptyCodeRule(this.RuleType);
            this.CodeRule.init(obj["CodeRule"]);
        }
    }

    convertToJObject():Object{
        let obj=new Object();
        obj["Id"]=this.Id;
        obj["Code"]=this.Code;
        obj["Name"]=this.Name;
        obj["Description"]=this.Description;
        obj["RuleType"]=this.RuleType;
        obj["CodeRule"]=this.CodeRule.convertToJObject();
        return obj;
    }
}


