import { HandlerType } from "./handlertype";
import { IDataHandler } from "./idatahandler";
import { ExpressHandler } from "./expresshandler";
import { ComponentHandler } from "./componenthandler";

export class DataHandlerFactory{
    public static getEmptyHandler(handlerType:HandlerType){
        //debugger;
        let handler:IDataHandler;
        if (handlerType == HandlerType.Express)
            {
                handler = new ExpressHandler();
            }
            else
            {
                handler = new ComponentHandler();
            }
            return handler;
    }
}


