import { IDataConvert } from "../idataconvert";
import { IDataHandler } from "../datahandler/idatahandler";
import { isNullOrUndefined } from "util";
import { HandlerSerializer } from "../datahandler/handlerserializer";

export class ParamMapping implements IDataConvert{
    
    ParamCode:string;
    Handler:IDataHandler;

    init(obj: object) {
        if(!isNullOrUndefined(obj)){
            this.ParamCode=obj["ParamCode"]==null?null:obj["ParamCode"];
            let handleObj=obj["Handler"];
            let handler:IDataHandler=HandlerSerializer.deSerializer(handleObj);
            this.Handler = handler;
        }
    }

    convertToJObject(): object {
        let obj=new Object();
        obj["ParamCode"]=this.ParamCode;
        let handlerJObj = HandlerSerializer.serializer(this.Handler);
        obj["Handler"]=handlerJObj;
        return obj;
    }
}


