import { AbstractionCodeRule } from "./abstractioncoderule";
import { ISegment } from "./segment/isegment";
import { SegmentType } from "./segment/segmenttype";
import { SegmentFactory } from "./segment/segmentfactory";
import { isNullOrUndefined } from "util";

export class BaseCodeRule extends AbstractionCodeRule {

    TYPE: string = "Type";
    CONTENT: string = "Content";
    Segments: Array<ISegment>=new Array<ISegment>();;
    IsBrokenUse: boolean;
    
    init(obj: object) {
        if(!isNullOrUndefined(obj)){
            super.init(obj);
            if (!isNullOrUndefined(obj["IsBrokenUse"])) {
                this.IsBrokenUse = obj["IsBrokenUse"] as boolean;
            }
            if(!isNullOrUndefined(obj["Segments"])){
                obj["Segments"].forEach(segmentObj => {
                    let segmentType:SegmentType=segmentObj[this.TYPE] as number;
                    let segment:ISegment=SegmentFactory.getEmptySegment(segmentType);
                    let contentObj:object=segmentObj[this.CONTENT];
                    segment.init(contentObj);
                    this.Segments.push(segment);
                });
            }
        }
    }

    convertToJObject():Object{
        //debugger;
        let obj=super.convertToJObject();
        obj["IsBrokenUse"]=this.IsBrokenUse;
        let segmentArray=new Array<Object>();
        if(this.Segments.length>0){
            this.Segments.forEach(segment => {
                let segmentObj=new Object();
                segmentObj[this.TYPE]=segment.getSegmentType() as number;
                segmentObj[this.CONTENT]=segment.convertToJObject();
                segmentArray.push(segmentObj);
            });
        }
        obj["Segments"]=segmentArray;
        return obj;
    }
}



