import { Component, OnInit, ViewChild, ViewContainerRef, ComponentFactory, ComponentRef, OnDestroy, ComponentFactoryResolver } from '@angular/core';
import { Http } from '@angular/http';
import { MetadataDto, GSPMetadataService } from '@gsp-lcm/metadata-selector';
import { CodeRuleMetadata } from './coderulemetadata/coderulemetadata';
import { isNullOrUndefined } from 'util';
import { BaseCodeRuleComponent } from './coderulecomponent/basecoderulecomponent/base.component';
import { CodeRuleType } from './coderulemetadata/coderuletype';
import { DMBaseCodeRuleComponent } from './coderulecomponent/dmbasecomponent/dmbase.component';
import { DynamicCodeRuleComponent } from './coderulecomponent/dynamiccomponent/dynamic.component';
import { DMDynamicCodeRuleComponent } from './coderulecomponent/dmdynamiccomponent/dmdynamic.component';
import { CacheService } from '@farris/ide-devkit/services';

@Component({
  selector: 'app-coderule',
  templateUrl: './coderule.component.html',
  styleUrls: ['./coderule.component.css']
})

export class CodeRuleDesignerComponent implements OnInit, OnDestroy {

  /**
   * 编号规则元数据
   */
  public codeRuleMetadata: CodeRuleMetadata;

  /**元数据抽象Dto */
  public metadataDto: MetadataDto;

  /**
   * 元数据的Uri,包含相对路径以及元数据文件名称信息
   */
  private _metadataUri: string;

  /**
   * 解析元数据的Uri，获取文件名称和路径
   */
  set metadataUri(metadataUri: string) {
    const fileName = metadataUri.substring(metadataUri.lastIndexOf('/') + 1);
    const filePath = metadataUri.substring(1, metadataUri.lastIndexOf('/'));
    this.metadataService.LoadMetadata(fileName, filePath).subscribe(gspMetadata => {
      this.metadataContent = gspMetadata;  //获取元数据Json串
    });
  }

  /**
   * 解析元数据Json串，获取编号规则元数据
   */
  set metadataContent(metadataContent: any) {
    this.loadCodeRuleMetadata(metadataContent);
  }

  private componentRef: ComponentRef<any>;

  @ViewChild("codeRuleComponentContainer", { read: ViewContainerRef }) container: ViewContainerRef;

  constructor(private http: Http, private metadataService: GSPMetadataService, private resolver: ComponentFactoryResolver, private cache: CacheService) { }

  ngOnInit() {
    this.codeRuleMetadata = new CodeRuleMetadata();
  }

  /**
   * 加载编号规则元数据
   * @param dto 元数据MetadataDto
   */
  private loadCodeRuleMetadata(dto: MetadataDto) {
    this.metadataDto = dto;
    if (!isNullOrUndefined(dto.content)) {
      this.codeRuleMetadata = new CodeRuleMetadata();
      let jsonObj = JSON.parse(dto.content);
      //debugger;
      this.codeRuleMetadata.init(jsonObj);
      this.codeRuleMetadata.Code = (isNullOrUndefined(this.codeRuleMetadata.Code)) ? dto.code : this.codeRuleMetadata.Code;
      this.codeRuleMetadata.Name = (isNullOrUndefined(this.codeRuleMetadata.Name)) ? dto.name : this.codeRuleMetadata.Name;
      this.createComponentByRuleType(this.codeRuleMetadata.RuleType);
    }
  }

  /**
   * 根据编号规则类型创建对应的组件
   * @param ruleType 编号规则类型
   */
  private createComponentByRuleType(ruleType: CodeRuleType) {
    switch (ruleType) {
      case CodeRuleType.Base:
        // 创建Base组件
        this.createComponent(this.codeRuleMetadata, BaseCodeRuleComponent);
        break;
      case CodeRuleType.DMBase:
        // 创建DMBase组件
        this.createComponent(this.codeRuleMetadata, DMBaseCodeRuleComponent);
        break;
      case CodeRuleType.Dynamic:
        // 创建Dynamic组件
        this.createComponent(this.codeRuleMetadata, DynamicCodeRuleComponent);
        break;
      case CodeRuleType.DMDynamic:
        // 创建DMDynamic组件
        this.createComponent(this.codeRuleMetadata, DMDynamicCodeRuleComponent);
        break;
      default:
        break;
    }
  }

  /**
   * 动态创建组件
   * @param metadata 
   */
  private createComponent(metadata: CodeRuleMetadata, componentTemplate: any) {
    this.container.clear();
    const factory: any = this.resolver.resolveComponentFactory(componentTemplate);
    this.componentRef = this.container.createComponent(factory);
    this.componentRef.instance.codeRuleMetadata = metadata;
    this.componentRef.instance.metadataDto = this.metadataDto;
    // this.componentRef.instance.output.subscribe(data => {
    //   this.codeRuleMetadata = data;
    // });
  }

  /**
  * 保存元数据
  */
  private saveCodeRuleMetadata() {
    //debugger;
    let jsonStringifyContent = JSON.stringify(this.codeRuleMetadata.convertToJObject());
    this.metadataDto.content = jsonStringifyContent;
    let sessionId = this.cache.get('sessionId');
    this.metadataService.SaveMetadata(this.metadataDto, sessionId).subscribe(response => {
      if (response.status == 200) {
        alert("保存成功！");
      }
      else {
        alert("保存失败!");
      }
    })
  }

  /**
   * 组件销毁
   */
  ngOnDestroy() {
    if (!isNullOrUndefined(this.componentRef)) {
      this.componentRef.destroy();
    }
  }
}



