import { GSP, Package } from '@farris/ide-devkit';
import { ComponentFactoryResolver, Injector } from '@angular/core';
import { CodeRuleDesignerPackageView, CodeRuleWizardPackageView } from './views';
import { CodeRuleDesignerComponent } from './views/coderuledesigner/coderule.component';
import { MetadataDto } from '../../../reference/metadata/metadata.ref';
import { CodeRuleWizardComponent } from './views/coderuledesigner/coderulewizard/coderulewizard.component';

export class CodeRuleDesignerPackage extends Package {

  private layoutPanel: any;

  constructor(
    gsp: GSP,
    private cfr: ComponentFactoryResolver,
    private injector: Injector
  ) {
    super(gsp);
  }

  activate(state) {
    this.gsp.workspace.addOpener((uri: string) => this.openCodeRule(uri));
    this.gsp.eventBus.on(null, null, 'Command:openMetadataGuide:CodeRule', this, (data) => this.openCodeRuleWizard(state, data));
  }

  openCodeRule(uri) {
    if (uri.endsWith('.coderule')) {
      const pkgView = new CodeRuleDesignerPackageView({}, this.cfr, this.injector);
      const currentElement = pkgView.getElement().instance as CodeRuleDesignerComponent;
      currentElement.metadataUri = uri;
      const fileName = uri.substring(uri.lastIndexOf('/') + 1);
      return {
        getTitle: () => fileName,
        getElement: () => pkgView.getElement()
      };
    }
  }

  /**
   * 打开编号规则向导
   * @param state state
   * @param dto 元数据MetadataDto
   */
  openCodeRuleWizard(state: any, dto: MetadataDto) {
    const packageView = new CodeRuleWizardPackageView(state, this.cfr, this.injector);
    const element = packageView.getElement();
    const guide = element.instance as CodeRuleWizardComponent;
    guide.createMetadataSuccess.subscribe((data)=>{
      //debugger;
      this.addNewTreeNode(data); 
    });
    guide.createMetadataEvent.subscribe((data) => {
      this.createMetadata(data);
      // this.refreshTree();
      this.openMetadata(data);
    });
    this.showGuide(element, guide, '编号规则定义向导', dto);
  }

  addNewTreeNode(nodeInfo) {
    this.gsp.eventBus.requestFor('FileTreePanelPackage', 'projectCreate', 'AccessFileTree', nodeInfo, () => {
      // console.log('event has been deal done');
    });
  }


  /**
   * 显示向导模态框
   */
  showGuide(ele: any, guide: CodeRuleWizardComponent, title: string, dto: MetadataDto) {
    guide.metadataContent = dto;
    const modalPanel = this.gsp.workspace.addModalPanel({
      item: {
        element: ele,
        modalOptions: {
          title: title,
          width: 400,
          height:150,
          buttons: guide.btns,
          resizable:false,
          showMaxButton:false
        }
      }
    });
    guide.closeModal.subscribe(() => modalPanel.destroy());
  }

  /**
   * 新建元数据
   */
  private createMetadata(data: MetadataDto) {
    this.gsp.eventBus.post(CodeRuleDesignerPackage, this.id, 'Command:CreateMetadata', data);
  }

  /**
   * 刷新树
   */
  refreshTree() {
    this.gsp.eventBus.requestFor('FileTreePanelPackage', 'metadataCreate', 'AccessFileTree', { action: '5' }, () => {
      console.log('refresh tree event has been deal done');
    });
  }
  /**
   * 打开元数据
   * @param data
   */
  private openMetadata(data: MetadataDto) {
    const uri = this.getUriFromMetadataDto(data);
    this.gsp.workspace.open(uri);
  }

  /**
 * 根据dto获取uri
 */
  private getUriFromMetadataDto(dto: MetadataDto): string {
    if (dto) {
      const fileName = dto.fileName;
      const filePath = dto.relativePath;
      return `/${filePath}/${fileName}`;
    }
  }
}



