import { IDataConvert } from "../../idataconvert";
import { IDataType } from "./datatype/idatatype";
import { isNullOrUndefined } from "util";
import { DataTypeEnum } from "./datatype/datatypeenum";
import { DataTypeFactory } from "./datatype/datatypefactory";

export class InputParameter implements IDataConvert{
    
    Code:string;
    Name:string;
    DataType:IDataType;
    
    init(obj: object) {
        if(!isNullOrUndefined(obj)){
            this.Code=obj["Code"]==null?null:obj["Code"];
            this.Name=obj["Name"]==null?null:obj["Name"];
            let dataTypeObj=obj["DataType"];
            if(!isNullOrUndefined(dataTypeObj)){
                let dataTypeInt:number=dataTypeObj["Type"] as number;
                let dtEnum:DataTypeEnum=dataTypeInt as DataTypeEnum;
                this.DataType = DataTypeFactory.getEmptyDataType(dtEnum);
                this.DataType.init(dataTypeObj["Content"]);
            }
        }
    }

    convertToJObject(): object {
        let obj=new Object();
        obj["Code"]=this.Code;
        obj["Name"]=this.Code;
        let dataTypeObj=new Object();
        let type:DataTypeEnum=this.DataType.getDataType();
        dataTypeObj["Type"]=type as number;
        dataTypeObj["Content"]=this.DataType.convertToJObject();
        obj["DataType"]=dataTypeObj;
        return obj;
    }
}


