import { IDataConvert } from "../idataconvert";
import { isNullOrUndefined } from "util";

export class AbstractionCodeRule implements IDataConvert {
    
    Id: string;
    Code: string;
    Name: string;
    Description: string;

    init(obj: object) {
        if (!isNullOrUndefined(obj)) {
            this.Id = obj["Id"] == null ? null : obj["Id"];
            this.Code = obj["Code"] == null ? null : obj["Code"];
            this.Name = obj["Name"] == null ? null : obj["Name"];
            this.Description = obj["Description"] == null ? null : obj["Description"];
        }
    }

    convertToJObject(): object {
        let obj=new Object();
        obj["Id"]=this.Id;
        obj["Code"]=this.Code;
        obj["Name"]=this.Name;
        obj["Description"]=this.Description;
        return obj;
    }
}


