import { PackageView } from '@farris/ide-devkit';
import { ComponentFactoryResolver, Injector } from '@angular/core';
import { CodeRuleDesignerComponent } from './coderuledesigner/coderule.component';
import { CodeRuleDesignerModule } from './coderuledesigner/coderule.module';
import { CodeRuleWizardComponent } from './coderuledesigner/coderulewizard/coderulewizard.component';
export class CodeRuleDesignerPackageView extends PackageView {

  constructor(serializedState: any,
    private cfr: ComponentFactoryResolver,
    private injector: Injector) {
    super(serializedState);
    const componentFactory = this.cfr.resolveComponentFactory(CodeRuleDesignerComponent);
    const moduleRef = this.injector.get(CodeRuleDesignerModule) as CodeRuleDesignerModule;
    console.log(moduleRef);
    this.element = componentFactory.create(this.injector);
  }
}

export class CodeRuleWizardPackageView extends PackageView {

  constructor(serializedState: any,
    private cfr: ComponentFactoryResolver,
    private injector: Injector) {
    super(serializedState);
    const componentFactory = this.cfr.resolveComponentFactory(CodeRuleWizardComponent);
    const moduleRef = this.injector.get(CodeRuleDesignerModule) as CodeRuleDesignerModule;
    console.log(moduleRef);
    this.element = componentFactory.create(this.injector);
  }
}




