import { NgModule, CUSTOM_ELEMENTS_SCHEMA } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { CommonModule } from '@angular/common';
// import { ToolBarModule } from '@progress/kendo-angular-toolbar';
import { ButtonModule, ButtonsModule, ButtonGroupModule } from '@progress/kendo-angular-buttons';
import { LayoutModule, SplitterModule } from '@progress/kendo-angular-layout';
import { TreeViewModule } from '@progress/kendo-angular-treeview';
import { GridModule } from '@progress/kendo-angular-grid';
import { DialogModule } from '@progress/kendo-angular-dialog';
import { InputsModule } from '@progress/kendo-angular-inputs';
import { DropDownsModule, DropDownListModule } from '@progress/kendo-angular-dropdowns';
import { PopupModule } from '@progress/kendo-angular-popup';
import { ReactiveFormsModule } from '@angular/forms';
import { FarrisDialogModule } from '@farris/ui';
import { LabelModule } from '@progress/kendo-angular-label';
import { GSPMetadataServiceModule, SelectMetadataComponent, MetadataSelectModule } from "@gsp-lcm/metadata-selector";
import { environment } from '../../../../environments/environment'
import { CodeRuleDesignerComponent } from './coderule.component';
import { BaseCodeRuleComponent } from './coderulecomponent/basecoderulecomponent/base.component';
import { CodeRuleWizardComponent } from './coderulewizard/coderulewizard.component';
import { DMBaseCodeRuleComponent } from './coderulecomponent/dmbasecomponent/dmbase.component';
import { DynamicCodeRuleComponent } from './coderulecomponent/dynamiccomponent/dynamic.component';
import { DMDynamicCodeRuleComponent } from './coderulecomponent/dmdynamiccomponent/dmdynamic.component';
import { OutParamSegmentComponent } from './coderulecomponent/commoncomponent/outparamcomponent/outparam.component';
import { SysVariableSegmentComponent } from './coderulecomponent/commoncomponent/syscomponent/sys.component';
import { SequenceSegmentComponent } from './coderulecomponent/commoncomponent/sequencecomponent/sequence.component';
import { ConstantSegmentComponent } from './coderulecomponent/commoncomponent/constcomponent/const.component';
import { CodeRuleService } from './coderuleservice/coderuleservice';
import { EventBusService } from './coderuleservice/eventBusService';
import { BaseOutParamSegmentComponent } from './coderulecomponent/commoncomponent/baseoutparamcomponent/baseoutparam.component';
import { ExpressionModule } from '@gsp-svc/expression';

@NgModule({
  declarations: [
    CodeRuleDesignerComponent,
    BaseCodeRuleComponent,
    CodeRuleWizardComponent,
    DMBaseCodeRuleComponent,
    DynamicCodeRuleComponent,
    DMDynamicCodeRuleComponent,
    OutParamSegmentComponent,
    SysVariableSegmentComponent,
    ConstantSegmentComponent,
    SequenceSegmentComponent,
    BaseOutParamSegmentComponent
  ],
  imports: [
    FormsModule,
    HttpModule,
    CommonModule,
    ExpressionModule,
    ButtonsModule,
    ButtonGroupModule,
    DialogModule,
    ReactiveFormsModule,
    InputsModule,
    DropDownListModule,
    PopupModule,
    TreeViewModule,
    LayoutModule,
    LabelModule,
    GridModule,
    LayoutModule,
    FarrisDialogModule,
    MetadataSelectModule,
    GSPMetadataServiceModule.forRoot(environment.SERVER_IP)
  ],
  providers: [CodeRuleService,EventBusService],
  entryComponents:[BaseCodeRuleComponent,CodeRuleWizardComponent,DMBaseCodeRuleComponent,
    DynamicCodeRuleComponent,DMDynamicCodeRuleComponent,OutParamSegmentComponent,SysVariableSegmentComponent,
    ConstantSegmentComponent,SequenceSegmentComponent,SelectMetadataComponent,BaseOutParamSegmentComponent],
  schemas: [CUSTOM_ELEMENTS_SCHEMA],
  bootstrap: [CodeRuleDesignerComponent]
})

export class CodeRuleDesignerModule { }



