import { IDataHandler } from "../../datahandler/idatahandler";
import { IDataConvert } from "../../idataconvert";
import { HandlerSerializer } from "../../datahandler/handlerserializer";
import { CodeRuleRefering } from "./coderulerefering";

export class Selector implements IDataConvert{
    
    Condition:IDataHandler;
    Refering:CodeRuleRefering;

    init(obj: object) {
        this.Condition = HandlerSerializer.deSerializer(obj["Condition"]);
    }

    convertToJObject(): object {
        let obj=new Object();
        let conditionJobj=HandlerSerializer.serializer(this.Condition);
        obj["Condition"]=conditionJobj;
        obj["Refering"]=this.Refering.convertToJObject();
        return obj;
    }
}


