import { Component, Input, OnInit } from '@angular/core';
import { ConstantSegment } from '../../../coderulemetadata/base/segment/constantsegment';
import { CodeRuleService } from '../../../coderuleservice/coderuleservice';

@Component({
    selector: 'app-const',
    templateUrl: './const.component.html'
})

export class ConstantSegmentComponent implements OnInit {
    /**
     * 编号规则元数据
    */
    @Input() segment: ConstantSegment;

    status: Array<boolean> = [true, false];

    segmentType:string;

    constructor(private codeRuleService:CodeRuleService) { }

    ngOnInit(): void {
        //debugger;
        let segmentTypeString =this.codeRuleService.getSegmentTypeToString(this.segment.getSegmentType());
        this.segmentType=this.codeRuleService.getSegmentTypeDesc(segmentTypeString);
    }
}


