import { Component, Input, OnInit, ViewContainerRef, ViewChild, ComponentFactoryResolver, ComponentRef, Injector, OnChanges, SimpleChanges } from '@angular/core';
import { CodeRuleMetadata } from '../../coderulemetadata/coderulemetadata';
import { SelectMetadataComponent, GSPMetadataService } from '../../../../../../../reference/metadata/metadata.ref';
import { ISegment } from '../../coderulemetadata/base/segment/isegment';
import { DialogComponent, BsModalService } from '@farris/ui';
import { isNullOrUndefined } from 'util';
import { isNullOrEmptyString } from '@progress/kendo-angular-grid/dist/es2015/utils';
import { ConstantSegment } from '../../coderulemetadata/base/segment/constantsegment';
import { SysVariableSegment } from '../../coderulemetadata/base/segment/sysvariablesegment';
import { OutParamSegment } from '../../coderulemetadata/base/segment/outparamsegment';
import { SequenceSegment } from '../../coderulemetadata/base/segment/sequencesegment';
import { ConstantSegmentComponent } from '../commoncomponent/constcomponent/const.component';
import { OutParamSegmentComponent } from '../commoncomponent/outparamcomponent/outparam.component';
import { SysVariableSegmentComponent } from '../commoncomponent/syscomponent/sys.component';
import { SequenceSegmentComponent } from '../commoncomponent/sequencecomponent/sequence.component';
import { DMBaseCodeRule } from '../../coderulemetadata/extension/dmbasecoderule';
import { CodeRuleService } from '../../coderuleservice/coderuleservice';
import { MetadataDto, Metadata4RefDto } from '@gsp-lcm/metadata-selector/metadata/metadata4refdto';
import { DMExtensionInfo } from '../../coderulemetadata/extension/dmextensioninfo';
import { BrokenObject, Item } from '../../commonobject/commonobject';
import { RowArgs, SelectAllCheckboxState } from '@progress/kendo-angular-grid';
import { brokenTypes } from '../../commonobject/const';
import { SegmentType } from '../../coderulemetadata/base/segment/segmenttype';
import { ParamMapping } from '../../coderulemetadata/extension/parammapping';
import { ExpressHandler } from '../../coderulemetadata/datahandler/expresshandler';
import { EventBusService } from '../../coderuleservice/eventBusService';
import { HandlerType } from '../../coderulemetadata/datahandler/handlertype';
import { CacheService } from '@farris/ide-devkit/services';
import { GSPBusinessEntity } from '@gsp-bef/gsp-be-metadata';

@Component({
    selector: 'app-dmbasecoderule',
    templateUrl: './dmbase.component.html',
    styleUrls: ['./dmbase.component.css']
})

export class DMBaseCodeRuleComponent implements OnInit,OnChanges {
    /**
     * 编号规则元数据
    */
    @Input() codeRuleMetadata: CodeRuleMetadata;

    /**
     * 元数据抽象类
     */
    @Input() metadataDto: MetadataDto;

    /**
     * 动态组件模板
     */
    private componentRef: ComponentRef<any>;

    /**
     * 段列表数据源,绑定Kendo UI Grid
     */
    brokenTypeList: Array<Item> =brokenTypes;

    /**
     * 模型类型列表
     */
    modelTypeList: Array<string> = ['ViewModel',"BusinessEntity"];

    status: Array<boolean> = [true, false];

    isBrokenUse:boolean=false;

    /**be实体 */
    beEntity:GSPBusinessEntity;

    /**
     * 已选择的段类型
     */
    selectedBrokenType: string;
    newBrokenCode:string;
    newBrokenName:string;
    selectedModel: string;
    dmExtensionInfo: DMExtensionInfo;
    dMBaseCodeRule: DMBaseCodeRule;
    brokenList:Array<BrokenObject>=[];
    selectedBrokenList:Array<BrokenObject>=[];
    selectedBrokenCodes: string[] = [];
    selectedBroken:BrokenObject;
    selectedSegement:ISegment;
    selectAllBrokenState: SelectAllCheckboxState = 'unchecked';
    codeRuleExample:string;
    isRowParamSelected = (e: RowArgs) => this.selectedBrokenCodes.indexOf(e.dataItem.Code) >= 0;
    @ViewChild("brokenType", { read: ViewContainerRef }) container: ViewContainerRef;
    @ViewChild('brokenSelection') brokenSelection: DialogComponent;
    @ViewChild('modelSelect') modelSelect: DialogComponent;

    constructor(private resolver: ComponentFactoryResolver,private modalService: BsModalService, private injector: Injector, 
        private codeRuleService: CodeRuleService,private codeRuleEventBus:EventBusService,private metadataService:GSPMetadataService,private cacheService:CacheService) { 
            this.codeRuleEventBus.eventbus.subscribe(_event => {
                this.codeRuleEventBusHandler(_event);
              })
        }
  

    ngOnInit(): void {
        if (!this.codeRuleMetadata.CodeRule) {
            this.codeRuleMetadata.CodeRule = new DMBaseCodeRule();
        }
        this.dMBaseCodeRule = this.codeRuleMetadata.CodeRule as DMBaseCodeRule;
        if (!this.dMBaseCodeRule.Segments) {
            this.dMBaseCodeRule.Segments = new Array<ISegment>();
        }
        if (!(this.codeRuleMetadata.CodeRule as DMBaseCodeRule).ExtensionInfo) {
            (this.codeRuleMetadata.CodeRule as DMBaseCodeRule).ExtensionInfo = new DMExtensionInfo();
        }
        if(this.dMBaseCodeRule.Segments.length>0){
            this.initSegment();
        }
        this.dmExtensionInfo = (this.codeRuleMetadata.CodeRule as DMBaseCodeRule).ExtensionInfo;
        this.isBrokenUse=this.dMBaseCodeRule.IsBrokenUse;
    }

    ngOnChanges(changes: SimpleChanges): void{
        
    }

    /**
     * 初始化段信息
     */
    initSegment(){
        this.dMBaseCodeRule.Segments.forEach(segment => {
            let broken=new BrokenObject();
            broken.Code=segment.SegmentCode;
            let segmentTypeString =this.codeRuleService.getSegmentTypeToString(segment.getSegmentType());
            broken.Type=this.codeRuleService.getSegmentTypeDesc(segmentTypeString);
            this.brokenList.push(broken);
        });
        this.selectedBroken= this.brokenList[0];
        this.selectedBrokenCodes=[];
        this.selectedBrokenCodes.push(this.brokenList[0].Code);
        this.selectedBrokenList=new Array<BrokenObject>();
        this.selectedBrokenList.push(this.brokenList[0]);
        this.createFocusedBrokenComponent();
    }

    codeRuleEventBusHandler(_event:any){
        if (_event && _event.eventName === "dmOutParamExpressEmit") {
            this.dMBaseCodeRule.ExtensionInfo=this.dMBaseCodeRule.ExtensionInfo==null?new DMExtensionInfo():this.dMBaseCodeRule.ExtensionInfo;
            this.dMBaseCodeRule.ExtensionInfo.Mappings=this.dMBaseCodeRule.ExtensionInfo.Mappings==null?new Array<ParamMapping>():this.dMBaseCodeRule.ExtensionInfo.Mappings;
            let tempIndex=this.dMBaseCodeRule.ExtensionInfo.Mappings.findIndex(mapping=>mapping.ParamCode==this.selectedBroken.Code);
            let mapping=this.dMBaseCodeRule.ExtensionInfo.Mappings[tempIndex];
            let expressHandler=new ExpressHandler();
            expressHandler.Express=_event.outParamExpress;
            if(!mapping.Handler){
                mapping.Handler=new ExpressHandler();
            }
            mapping.Handler=expressHandler;
            this.codeRuleService.replaceSpecifiedElementByCode(this.dMBaseCodeRule.ExtensionInfo.Mappings,mapping);
          }
    }

    /**
     * 根据左侧列表获取对应的段实例
     * @param brokenCode 
     */
    getFocusedSegment(brokenCode:string):ISegment{
        let tempIndex=this.dMBaseCodeRule.Segments.findIndex(segment=>segment.SegmentCode==brokenCode);
        if(tempIndex!=(-1)){
            return this.dMBaseCodeRule.Segments[tempIndex];
        }
        else{
            return null;
        }
    }

    /**
     * 创建当前选中焦点行的对应Broken组件
     */
    createFocusedBrokenComponent(){
        let segement=this.getFocusedSegment(this.selectedBroken.Code);
        this.selectedSegement=segement;
        let segmentType=segement.getSegmentType();
        let segmentTypeString=this.codeRuleService.getSegmentTypeToString(segmentType);
        this.createComponentByBrokenType(segmentTypeString,segement);
    }
   

    /**
     * 打开元数据模态框
     */
    openMetadataDialog() {
        this.openModelSelectDialog();
    }

    /**
     * 模型选择确定
     */
    modelSelectConfirm() {
        if (isNullOrUndefined(this.selectedModel) || isNullOrEmptyString(this.selectedModel)) {
            alert('请选择模型类型。');
            return;
        }
        this.modelSelect.close();
        this.openMetadataSelect();
    }

    /**
     * 打开模型选择模态框
     */
    openModelSelectDialog() {
        this.modelSelect.title = '模型类型选择';
        this.modelSelect.width = 400;
        this.modelSelect.height = 160;
        this.modelSelect.showMaxButton = false;
        this.modelSelect.resizable = false;
        this.modelSelect.show();
    }

    /**
     * 动态创建元数据选择界面组件并打开
     */
    private openMetadataSelect() {
        //debugger;
        const compFactory = this.resolver.resolveComponentFactory(SelectMetadataComponent);
        const compRef = compFactory.create(this.injector);
        compRef.instance.metadataPath = this.metadataDto.relativePath;
        compRef.instance.typeName = this.selectedModel=="ViewModel"?"GSPViewModel":"GSPBusinessEntity";
    
        compRef.instance.selectedMetadata.subscribe($event => {
            //debugger;
            let metadata4RefDto: Metadata4RefDto = $event;
            if(this.selectedModel == "ViewModel"){
                this.getViewModelInfo(metadata4RefDto);
            }
            else{
                this.getBEInfo(metadata4RefDto);
            }
        });
        const modalConfig = {
            title: '元数据选择',
            width: 882,
            height: 573,
            showButtons: false
        };
        const dialog = this.modalService.show(compRef, modalConfig);
        compRef.instance.closeModalEmitter.subscribe($event => {
            dialog.close();
        });
    }

    /**
     * 获取VM的信息，VM内部的Code和Name可能与元数据Header中的不一致，目前先用Header中的Code和Name
     * @param metadata4RefDto 元数据引用Dto
     */
    getViewModelInfo(metadata4RefDto: Metadata4RefDto) {
        this.dmExtensionInfo.ModelId = metadata4RefDto.metadata.id;
        this.dmExtensionInfo.ModelCode = metadata4RefDto.metadata.code;
        this.dmExtensionInfo.ModelName = metadata4RefDto.metadata.name;
        this.dmExtensionInfo.ModelPath = metadata4RefDto.metadata.relativePath;
    }
    /**
     * 获取BE信息，使用Header中的Code和Name
     * @param metadata4RefDto 
     */
    getBEInfo(metadata4RefDto: Metadata4RefDto){
        //debugger;
        this.dmExtensionInfo.ModelId = metadata4RefDto.metadata.id;
        this.dmExtensionInfo.ModelCode = metadata4RefDto.metadata.code;
        this.dmExtensionInfo.ModelName = metadata4RefDto.metadata.name;
        this.dmExtensionInfo.ModelPath = metadata4RefDto.metadata.relativePath;
        let sessionId = this.cacheService.get('sessionId');
        this.metadataService.GetRefMetadata(metadata4RefDto.metadata.relativePath,metadata4RefDto.metadata.id,sessionId).subscribe(data=>{
            //debugger;
            const beData =data as MetadataDto;
            this.beEntity=JSON.parse(beData.content) as GSPBusinessEntity;
        })
    }


    deleteModel(){
        this.dMBaseCodeRule.ExtensionInfo=new DMExtensionInfo();
        this.dMBaseCodeRule.ExtensionInfo.Mappings=new Array<ParamMapping>();
        this.dmExtensionInfo=this.dMBaseCodeRule.ExtensionInfo;
    }

    /**
     * 新增段
     */
    add() {
        this.selectedBrokenType='Constant';
        this.openBrokenSelectionDialog();
    }

    /**
     * 删除段
     */
    delete(){
        if (!this.selectedBrokenList || this.selectedBrokenList.length <= 0) {
            alert("请选择需要删除的段。");
            return;
        }
        this.selectedBrokenList.forEach(broken => {
            this.codeRuleService.deleteSpecifiedElementByCode(this.brokenList, broken);
            let segement=this.getFocusedSegment(broken.Code);
            this.codeRuleService.deleteSpecifiedElement(this.dMBaseCodeRule.Segments,segement);
            if(segement.getSegmentType()==SegmentType.OutParam){
                let tempIndex=this.dMBaseCodeRule.ExtensionInfo.Mappings.findIndex(mapping=>mapping.ParamCode==segement.SegmentCode);
                this.codeRuleService.deleteSpecifiedElementByCode(this.dMBaseCodeRule.ExtensionInfo.Mappings,this.dMBaseCodeRule.ExtensionInfo.Mappings[tempIndex]);
            }
        });
        this.resetFocused();
        this.selectAllBrokenState = 'unchecked';
        this.selectedBrokenCodes=[];
        this.selectedBrokenList=[];
    }

    up(){
        if(this.selectedBrokenCodes.length<=0){
            alert('请选择上移的段。');
            return;
        }
        if(isNullOrUndefined(this.selectedSegement.SegmentCode)||isNullOrUndefined(this.selectedSegement.SegmentCode)){
            alert('请选择上移的段。');
            return;
        }
        let tempIndex=this.brokenList.findIndex(broken=>broken.Code==this.selectedBroken.Code);
        if(tempIndex==0){
            alert('无法上移。');
            return;
        }
        else{
            let tempSegments=new Array<ISegment>();
            for(let i=0;i<this.dMBaseCodeRule.Segments.length;i++){
                if(i==tempIndex-1){
                    tempSegments.push(this.dMBaseCodeRule.Segments[tempIndex]);
                }
                else if(i==tempIndex){
                    tempSegments.push(this.dMBaseCodeRule.Segments[tempIndex-1]);
                }
                else{
                    tempSegments.push(this.dMBaseCodeRule.Segments[i]);
                }
            }
            this.brokenList=new Array<BrokenObject>();
            tempSegments.forEach(temp => {
                let broken=new BrokenObject;
                broken.Code=temp.SegmentCode;
                let segmentTypeString =this.codeRuleService.getSegmentTypeToString(temp.getSegmentType());
                broken.Type=this.codeRuleService.getSegmentTypeDesc(segmentTypeString);
                this.brokenList.push(broken);
            });
            this.dMBaseCodeRule.Segments=new Array<ISegment>();
            this.dMBaseCodeRule.Segments=tempSegments;
        }
    }

    down(){
        if(this.selectedBrokenCodes.length<=0){
            alert('请选择下移的段。');
            return;
        }
        if(isNullOrUndefined(this.selectedSegement.SegmentCode)||isNullOrUndefined(this.selectedSegement.SegmentCode)){
            alert('请选择下移的段。');
            return;
        }
        let tempIndex=this.brokenList.findIndex(broken=>broken.Code==this.selectedBroken.Code);
        if(tempIndex==this.brokenList.length-1){
            alert('无法下移。');
            return;
        }
        else{
            let tempSegments=new Array<ISegment>();
            for(let i=0;i<this.dMBaseCodeRule.Segments.length;i++){
                if(i==tempIndex){
                    tempSegments.push(this.dMBaseCodeRule.Segments[tempIndex+1]);
                }
                else if(i==tempIndex+1){
                    tempSegments.push(this.dMBaseCodeRule.Segments[tempIndex]);
                }
                else{
                    tempSegments.push(this.dMBaseCodeRule.Segments[i]);
                }
            }
            this.brokenList=new Array<BrokenObject>();
            tempSegments.forEach(temp => {
                let broken=new BrokenObject;
                broken.Code=temp.SegmentCode;
                let segmentTypeString =this.codeRuleService.getSegmentTypeToString(temp.getSegmentType());
                broken.Type=this.codeRuleService.getSegmentTypeDesc(segmentTypeString);
                this.brokenList.push(broken);
            });
            this.dMBaseCodeRule.Segments=new Array<ISegment>();
            this.dMBaseCodeRule.Segments=tempSegments;
        }
    }
    
    /**
     * 重置焦点
     */
    resetFocused(){
        if(this.brokenList.length>0){
            this.selectedBroken=this.brokenList[0];
            this.createFocusedBrokenComponent();
        }
        else{
            if(!isNullOrUndefined(this.componentRef)){
                this.componentRef.destroy();
            }
        }
    }

    /**
     * 打开段选择模态框
     */
    openBrokenSelectionDialog() {
        this.brokenSelection.title = '段类型选择';
        this.brokenSelection.width = 400;
        this.brokenSelection.height = 230;
        this.brokenSelection.showMaxButton = false;
        this.brokenSelection.resizable = false;
        this.brokenSelection.show();
    }

    /**
     * 增加段确定
     */
    brokenSelectionConfirm() {
        if (isNullOrUndefined(this.selectedBrokenType) || isNullOrEmptyString(this.selectedBrokenType)) {
            alert('请选择段类型。');
            return;
        }
        this.brokenSelection.close();
        let segment =this.getSegmentByBrokenType(this.selectedBrokenType,this.newBrokenCode,this.newBrokenName);
        this.createComponentByBrokenType(this.selectedBrokenType,segment);
        this.dMBaseCodeRule.Segments.push(segment);
        let broken=new BrokenObject();
        broken.Code=segment.SegmentCode;
        broken.Type=this.codeRuleService.getSegmentTypeDesc(this.selectedBrokenType);
        this.brokenList.push(broken);
        if(this.selectedBrokenType=='OutParam'){
            this.dMBaseCodeRule.ExtensionInfo=this.dMBaseCodeRule.ExtensionInfo==null?new DMExtensionInfo():this.dMBaseCodeRule.ExtensionInfo;
            this.dMBaseCodeRule.ExtensionInfo.Mappings=this.dMBaseCodeRule.ExtensionInfo.Mappings==null?new Array<ParamMapping>():this.dMBaseCodeRule.ExtensionInfo.Mappings;
            let mapping=new ParamMapping();
            mapping.ParamCode=(segment as OutParamSegment).SegmentCode;
            let expressHandler=new ExpressHandler();
            expressHandler.Express="";
            mapping.Handler=expressHandler;
            this.dMBaseCodeRule.ExtensionInfo.Mappings.push(mapping);
        }
        //新增后焦点定位到新增的行
        this.resetSelectedBroken(this.brokenList[this.brokenList.length-1])
        //重置新增模态框的选择信息
        this.resetAddBrokenInfo();
    }

    resetAddBrokenInfo(){
        this.selectedBrokenType='';
        this.newBrokenCode='';
        this.newBrokenName='';
    }

    /**
     * 重置选择的段，目的是显示Kendo UI的选中焦点
     * @param broken 
     */
    resetSelectedBroken(broken:BrokenObject){
        this.selectedBrokenCodes=[];
        this.selectedBrokenCodes.push(broken.Code);
        this.selectedBroken=broken;
        this.selectedBrokenList=[];
        this.selectedBrokenList.push(this.selectedBroken);
    }

    /**
     * 根据段类型（字符串）获取对应的段实体类
     * @param brokenType 
     */
    private getSegmentByBrokenType(brokenType: string,brokenCode:string,brokenName:string):ISegment {
        switch (brokenType) {
            case 'Constant':
                let constantSegment: ConstantSegment = new ConstantSegment();
                constantSegment.SegmentCode = brokenCode;
                constantSegment.SegmentName=brokenName;
                return constantSegment;
            case 'SysVariable':
                let sysVariableSegment: SysVariableSegment = new SysVariableSegment();
                sysVariableSegment.SegmentCode =brokenCode;
                sysVariableSegment.SegmentName=brokenName;
                return sysVariableSegment;
            case 'OutParam':
                let outParamSegment: OutParamSegment = new OutParamSegment();
                outParamSegment.SegmentCode = brokenCode;
                outParamSegment.SegmentName=brokenName;
                return outParamSegment;
            case 'Sequence':
                let sequenceSegment: SequenceSegment = new SequenceSegment();
                sequenceSegment.SegmentCode = brokenCode;
                sequenceSegment.SegmentName = brokenName;
                return sequenceSegment;
            default:
                return null;;
        }
    }

    /**
     * 根据段类型（字符串）动态创建对应段类型的组件
     */
    private createComponentByBrokenType(brokenType: string,segment:ISegment) {
        switch (brokenType) {
            case 'Constant':
                this.createComponent(segment, ConstantSegmentComponent);
                break;
            case 'SysVariable':
                this.createComponent(segment, SysVariableSegmentComponent);
                break;
            case 'OutParam':
                this.createComponent(segment, OutParamSegmentComponent);
                this.dMBaseCodeRule.ExtensionInfo=this.dMBaseCodeRule.ExtensionInfo==null?new DMExtensionInfo():this.dMBaseCodeRule.ExtensionInfo;
                this.dMBaseCodeRule.ExtensionInfo.Mappings=this.dMBaseCodeRule.ExtensionInfo.Mappings==null?new Array<ParamMapping>():this.dMBaseCodeRule.ExtensionInfo.Mappings;
                if(this.dMBaseCodeRule.ExtensionInfo.Mappings.length>0){
                    let tempIndex=this.dMBaseCodeRule.ExtensionInfo.Mappings.findIndex(mapping=>mapping.ParamCode==segment.SegmentCode);
                    let temp=this.dMBaseCodeRule.ExtensionInfo.Mappings[tempIndex].Handler.getHandlerType();
                    if(this.dMBaseCodeRule.ExtensionInfo.Mappings[tempIndex].Handler.getHandlerType()==HandlerType.Express){
                        this.componentRef.instance.outParamExpress=(this.dMBaseCodeRule.ExtensionInfo.Mappings[tempIndex].Handler as ExpressHandler).Express;
                    }
                }
                else{
                    this.componentRef.instance.outParamExpress='';
                }
                break;
            case 'Sequence':
                this.createComponent(segment, SequenceSegmentComponent);
                break;
            default:
                break;
        }
    }

    /**
     * 动态创建段组件
     * @param segment 段类型
     * @param componentTemplate 组件模板
     */
    private createComponent(segment: ISegment, componentTemplate: any) {
        this.container.clear();
        const factory: any = this.resolver.resolveComponentFactory(componentTemplate);
        this.componentRef = this.container.createComponent(factory);
        this.componentRef.instance.segment = segment;
    }

    /**
    * 组件销毁
    */
    ngOnDestroy() {
        if(!isNullOrUndefined(this.componentRef)){
            this.componentRef.destroy();
        }
    }

    /**
     * Kendo UI Grid行改变事件
     * @param e 
     */
    onSelectedBrokenChange(e) {
        this.selectedBrokenList = [];
        let len = this.selectedBrokenCodes.length;
        if (len > 0) {
            this.selectedBrokenCodes.forEach(code => {
                if (this.brokenList && this.brokenList.length > 0) {
                    let tempIndex = this.brokenList.findIndex(value => value.Code == code);
                    if (tempIndex != (-1)) {
                        this.selectedBrokenList.push(this.brokenList[tempIndex]);
                    }
                }
            });
            //单选情况加添加该语句
            this.selectedBroken=this.selectedBrokenList[0];
            this.createFocusedBrokenComponent();
        }
        if (len === 0) {
            this.selectAllBrokenState = 'unchecked';
        } else if (len > 0 && len < this.brokenList.length) {
            this.selectAllBrokenState = 'indeterminate';
        } else {
            this.selectAllBrokenState = 'checked';
        }
    }

    /**
     * Kendo UI 列头CheckBox全选事件
     * @param checkedState 
     */
    onSelectAllBrokenChange(checkedState: SelectAllCheckboxState) {
        if (checkedState === 'checked') {
            this.selectedBrokenCodes = this.brokenList.map((item) => item.Code);
            this.selectAllBrokenState = 'checked';
        } else {
            this.selectedBrokenCodes = [];
            this.selectAllBrokenState = 'unchecked';
        }
    }

    /**
     * cell点击事件处理
     * @param param0 
     */
    cellClickHandler({ dataItem }) {
        this.selectedBroken=dataItem;
        this.createFocusedBrokenComponent();
        this.resetRowSelectionFocus(dataItem);
    }

    /**
     * 重置行选择焦点，Kendo UI Grid的行选择
     * @param dataTtem 
     */
    resetRowSelectionFocus(dataTtem: BrokenObject) {
        this.selectedBrokenCodes = [];
        this.selectedBrokenCodes.push(dataTtem.Code);
    }
}


