import { IDataType } from "./idatatype";
import { DataTypeEnum } from "./datatypeenum";
import { isNullOrUndefined } from "util";

export class ComplexType implements IDataType{
   
    
    ModelId:string;
    ModelName:string;

    getDescription(): string {
        return this.ModelName;
    }

    getDataType(): DataTypeEnum {
        return DataTypeEnum.ComplexType;
    }

    init(obj: object) {
        if(!isNullOrUndefined(obj)){
            this.ModelId=obj["ModelId"]==null?null:obj["ModelId"];
            this.ModelId=obj["ModelName"]==null?null:obj["ModelName"];
        }
    }

    convertToJObject(): object {
        let obj=new Object();
        obj["ModelId"]=this.ModelId;
        obj["ModelName"]=this.ModelName;
        return obj;
    }
}


