import { CodeRuleType } from "./coderuletype";
import { BaseCodeRule } from "./base/basecoderule";
import { AbstractionCodeRule } from "./base/abstractioncoderule";
import { DMBaseCodeRule } from "./extension/dmbasecoderule";
import { DMDynamicCodeRule } from "./extension/dmdynamiccoderule";
import { DynamicCodeRule } from "./base/dynamic/dynamiccoderule";

export class CodeRuleFactory {
    public static getEmptyCodeRule(type: CodeRuleType): AbstractionCodeRule {
        let codeRule: AbstractionCodeRule;
        if (type == CodeRuleType.Base) {
            codeRule = new BaseCodeRule();
        }
        else if (type == CodeRuleType.DMBase) {
            codeRule = new DMBaseCodeRule();
        }
        else if (type == CodeRuleType.Dynamic) {
            codeRule = new DynamicCodeRule();
        }
        else {
            codeRule = new DMDynamicCodeRule();
        }
        return codeRule;
    }
}


