import { Component, ViewChild, TemplateRef, EventEmitter } from '@angular/core';
import { Item } from '../commonobject/commonobject';
import { codeRuleTypes } from '../commonobject/const';
import { MetadataDto, GSPMetadataService } from '../../../../../../reference/metadata/metadata.ref';
import { isNullOrUndefined } from 'util';
import { CodeRuleMetadata } from '../coderulemetadata/coderulemetadata';
import { CodeRuleType } from '../coderulemetadata/coderuletype';


@Component({
    selector: 'app-coderulewizard',
    templateUrl: './coderulewizard.component.html'
})

export class CodeRuleWizardComponent {

    /**
    * 编号规则类型列表
    */
    private codeRuleTypeList: Array<Item> = codeRuleTypes;

    /**
    * 已选择的编号规则类型 
    */
    private selectedCodeRuleType: string;

    /**
    * 向导完成，新建元数据
    */
    createMetadataEvent: EventEmitter<any> = new EventEmitter<any>();

    createMetadataSuccess: EventEmitter<any> = new EventEmitter<any>();

    /**
    * 关闭窗口事件
    */
    closeModal = new EventEmitter<any>();

    /**
    * 元数据加载
    */
    _metadataContnet: MetadataDto;

    set metadataContent(metadataContent: any) {
        this._metadataContnet = metadataContent;
    }


    @ViewChild('btn') btns: TemplateRef<any>;

    constructor(private gspMetadataService: GSPMetadataService) {
        this.createMetadataEvent = new EventEmitter<any>();
    }

    /**
   * 确认
   */
    commit() {
        if (isNullOrUndefined(this.selectedCodeRuleType)) {
            alert('请选择编号规则类型。');
            return;
        }
        else if (this.selectedCodeRuleType != 'Base'&&this.selectedCodeRuleType != 'DMBase') {
            alert('暂未开发');
            return;
        }
        this.closeWindow();
        this.createCodeRule();
    }

    /**
     * 创建编号规则元数据
     */
    createCodeRule() {
        const metadatadto = this._metadataContnet;
        this.gspMetadataService.InitializeMetadataEntity(metadatadto).subscribe(response => {
            metadatadto.id = response.id;
            let codeRuleMetadata = new CodeRuleMetadata;
            let jsonObj = JSON.parse(response.content);
            codeRuleMetadata.init(jsonObj);
            codeRuleMetadata.RuleType = this.getCodeRuleType(this.selectedCodeRuleType);
            metadatadto.content = JSON.stringify(codeRuleMetadata);
            this.gspMetadataService.CreateMetadata(metadatadto).subscribe(saveResponse => {
                this.createMetadataSuccess.emit({
                    action: '1',
                    node: {
                      nodeID: metadatadto.fileName,
                      nodeName: metadatadto.fileName,
                      nodePath: metadatadto.relativePath,
                      nodeType: 'file'
                    }
                  });
                this.createMetadataEvent.emit(metadatadto);
            })
        })
    }

    getCodeRuleType(typeString: string): CodeRuleType {
        switch (typeString) {
            case 'Base':
                return CodeRuleType.Base;
            case 'DMBase':
                return CodeRuleType.DMBase;
            case 'Dynamic':
                return CodeRuleType.Dynamic;
            case 'DMDynamic':
                return CodeRuleType.DMDynamic;
            default:
                return null;
        }
    }

    /**
   * 取消
   */
    cancel() {
        this.closeWindow();
    }

    /**
     * 关闭
     */
    closeWindow() {
        this.closeModal.emit();
    }
}


