import { ISegment } from "./isegment";
import { SegmentType } from "./segmenttype";
import { isNullOrUndefined } from "util";

export class SequenceSegment implements ISegment{
    SegmentName: string;
    SegmentCode:string;
    IsUseShow:boolean;
    IsDimension:boolean;
    Start:Number;
    Step:number;
    Length:number;

    getSegmentType(): SegmentType {
        return SegmentType.Sequence;
    }   

    init(obj: object) {
        if(!isNullOrUndefined(obj)){
            if(!isNullOrUndefined(obj["IsUseShow"])){
                this.IsUseShow=obj["IsUseShow"] as boolean;
            }
            if(!isNullOrUndefined(obj["IsDimension"])){
                this.IsDimension=obj["IsDimension"] as boolean;
            }
            if(!isNullOrUndefined(obj["Start"])){
                this.Start=obj["Start"] as number;
            }
            if(!isNullOrUndefined(obj["Step"])){
                this.Step=obj["Step"] as number;
            }
            if(!isNullOrUndefined(obj["Length"])){
                this.Length=obj["Length"] as number;
            }
            this.SegmentCode=obj["SegmentCode"];
            this.SegmentName=obj["SegmentName"];
        }
    }

    convertToJObject(): object {
        let obj=new Object();
        obj["SegmentCode"]=this.SegmentCode;
        obj["SegmentName"]=this.SegmentName;
        obj["IsUseShow"]=this.IsUseShow;
        obj["IsDimension"]=this.IsDimension;
        obj["Start"]=this.Start;
        obj["Step"]=this.Step;
        obj["Length"]=this.Length;
        return obj;
    }
}


