import { IDataHandler } from "./idatahandler";
import { isNullOrUndefined } from "util";
import { HandlerType } from "./handlertype";
import { DataHandlerFactory } from "./datahandlerfactory";

export class HandlerSerializer {
    public static deSerializer(handlerObj: Object): IDataHandler {
        if (handlerObj == null) {
            return null;
        }
        //debugger;
        let typeInt: number = handlerObj["Type"];
        let handlerType: HandlerType = typeInt as HandlerType;
        let handler: IDataHandler = DataHandlerFactory.getEmptyHandler(handlerType);
        let handlerContentObj = handlerObj["Content"];
        handler.init(handlerContentObj);
        return handler;
    }

    public static serializer(handler: IDataHandler): Object {
        let handlerJObj = new Object();
        handlerJObj["Type"] = handler.getHandlerType();
        handlerJObj["Content"] = handler.convertToJObject();
        return handlerJObj;
    }
}


