import { ISegment } from "./isegment";
import { SegmentType } from "./segmenttype";
import { ConstantSegment } from "./constantsegment";
import { OutParamSegment } from "./outparamsegment";
import { SequenceSegment } from "./sequencesegment";
import { SysVariableSegment } from "./sysvariablesegment";

export class SegmentFactory {
    public static getEmptySegment(segmentType: SegmentType): ISegment {
        let segment: ISegment;
        if (segmentType == SegmentType.Constant) {
            segment = new ConstantSegment();
        }
        else if (segmentType == SegmentType.OutParam) {
            segment = new OutParamSegment();
        }
        else if (segmentType == SegmentType.Sequence) {
            segment = new SequenceSegment();
        }
        else {
            segment = new SysVariableSegment();
        }
        return segment;
    }
}


