import { AbstractionCodeRule } from "../abstractioncoderule";
import { InputParameter } from "./inputparameter";
import { Selector } from "./selector";
import { isNullOrUndefined } from "util";

export class DynamicCodeRule extends AbstractionCodeRule{
    Params:Array<InputParameter>;
    Selectors:Array<Selector>;
    init(obj:object){
        super.init(obj);
        this.Params=new Array<InputParameter>();
        if(!isNullOrUndefined(obj["Params"])){
            obj["Params"].forEach(paramObj => {
                let param=new InputParameter();
                param.init(paramObj);
                this.Params.push(param);
            });
        }
        this.Selectors=new Array<Selector>();
        if(!isNullOrUndefined(obj["Selectors"])){
            obj["Selectors"].forEach(selectorObj => {
                let selector=new Selector();
                selector.init(selectorObj);
                this.Selectors.push(selector);
            });
        }
    }

    convertToJObject():Object{
        let obj=super.convertToJObject();
        let paramObjArray=new Array<Object>();
        if(this.Params!=null&&this.Params.length>0){
            this.Params.forEach(param => {
                let paramObj=new Object();
                paramObjArray.push(param.convertToJObject());
            });
        }
        obj["Params"]=paramObjArray;
        let selectorObjArray=new Array<Object>();
        if(this.Selectors!=null&&this.Selectors.length>0){
            this.Selectors.forEach(selector => {
                selectorObjArray.push(selector)
            });
        }
        obj["Selectors"]=selectorObjArray;
        return obj;
    }
}


