import { Component, Input, OnInit } from '@angular/core';
import { SysVariableSegment } from '../../../coderulemetadata/base/segment/sysvariablesegment';
import { CodeRuleService } from '../../../coderuleservice/coderuleservice';
import { ExpressionService } from '@gsp-svc/expression/editor/expressionservice';
import { ExpressionContextEntity } from '@gsp-svc/expression/editor/expressioncontextentity';
import { FuncType } from '@gsp-svc/expression/editor/funcentity';
import { expression_entity } from '../expression/mockexpression';

@Component({
    selector: 'app-sys',
    templateUrl: './sys.component.html',
    providers: [ExpressionService]
})

export class SysVariableSegmentComponent implements OnInit {
    /**
     * 编号规则元数据
    */
    constructor(private codeRuleService: CodeRuleService,private expr: ExpressionService) { }
    expressionEntity = expression_entity
    @Input() segment: SysVariableSegment;

    status: Array<boolean> = [true, false];

    segmentType: string;

    example: string;

    ngOnInit(): void {
        let segmentTypeString = this.codeRuleService.getSegmentTypeToString(this.segment.getSegmentType());
        this.segmentType = this.codeRuleService.getSegmentTypeDesc(segmentTypeString);
    }

    openExpr() {
        //debugger;
        let exprText:string=this.segment.Express?JSON.parse(this.segment.Express).Expr:"默认文本";
        this.expr.addExpressionText(exprText);
        this.expressionEntity.sessionEntities.forEach(
            item => this.expr.addSession(item));
        this.expressionEntity.contextEntities.forEach(
            item => this.expr.addContext(item)
        )
        this.expressionEntity.funcEntities.forEach(
            item => this.expr.addFunc(item)
        )
        this.expr.buildExpression().then(expressioninfo => {
        //debugger;
            this.segment.Express =expressioninfo as string
        }
        );
    }






}


